@import '../viewport.scss';

.slider-card {
  padding: 0;
  margin: 0;
}

.slider-card__item {
  padding: 20px 20px 60px;
  background-color: #f3ebe1;
}

.slider-card__product {
  display: flex;
  flex-direction: column;
}

.slider-card__wrapper {
  margin: 0 auto;
  padding: 0;
}

.slider-card__text {
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  color: #7859cf;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 0.04em;
}

.slider-card__title {
  font-weight: 700;
  font-size: 36px;
  line-height: 48px;
  margin: 4px 0 0;
  letter-spacing: 0.02em;
}

.slider-card__description {
  margin-top: 10px;
  margin-bottom: 21px;
  text-align: center;
}

.slider-card__promo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider-card__price {
  margin-right: 6px;
  color: #7859cf;
  font-size: 16px;
  line-height: 32px;
  text-decoration-line: line-through;
}

.slider-card__new-price {
  font-weight: 600;
  font-size: 20px;
  line-height: 32px;
  margin-right: 20px;
}

.slider-card__link {
  color: #ffffff;
  padding: 8px 20px;
  background-color: #7859cf;
  border-radius: 4px;
  width: 113px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  box-sizing: border-box;
}

.slider-card__link:hover {
  background-color: #9070ec;
}

.slider-card__link:focus {
  background-color: #9070ec;
}

.slider-card__link:active {
  background-color: #593cac;
}

.slider-card__link:disabled {
  background-color: #bdbdbd;
}

.slider-card__link-image {
  display: block;
}

.slider-card__item2 {
  display: none;
  background-color: #eae6fc;
}

.slider-card__item3 {
  display: none;
  background-color: #e5e6e8;
}

@media (min-width: $tablet-width) {
  .slider-card__link-image {
    order: 5;
  }

  .slider-card__item {
    background: linear-gradient(to bottom,#f3ebe1 77%,#fff 77%);
    padding: 60px 80px 0;
  }

  .slider-card__title {
    margin: 5px 0 5px;
    font-size: 56px;
    line-height: 80px;
  }

  .slider-card__description {
    font-size: 20px;
    line-height: 32px;
  }

  .slider-card__promo {
    padding-bottom: 20px;
  }

  .slider-card__logo-mobile {
    width: 476px;
    height: 476px;
  }
}
