@import '../viewport.scss';

.slider-control__button {
  background-color: transparent;
  border: none;
}

.slider-control__left {
  position: absolute;
  top: 48%;
  left: -1px;
}

.slider-control__right {
  position: absolute;
  top: 48%;
  right: -1px;
}

@media (min-width: $tablet-width) {
  .slider-control__left {
    top: 37%;
    left: 29px;
  }

  .slider-control__right {
    top: 37%;
    right: 29px;
  }

}
