@import '../viewport.scss';

.main-footer__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 16px 20px;
  border-bottom: 1px solid #f0ebff;
}

.main-footer__logo-image--tablet-and-desktop {
  display: none;
}

@media (min-width: $tablet-width) {
  .main-footer__wrapper {
    border: none;
  }
}
