.social__list {
  display: flex;
  margin: 0;
  padding: 0;
}

.social__item {
  margin-right: 20px;
  width: 40px;
  height: 40px;

  &:last-child {
    margin-right: 0;
  }
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.social__icon {
  fill: #bdbdbd;
}

.social__link:hover .social__icon {
  fill: #7859cf;
}

.social__link:hover {
  background-color: #f0ebff;
}

.social__link:focus .social__icon {
  fill: #7859cf;
}

.social__link:focus {
  background-color: #f0ebff;
}

.social__link:active .social__icon {
  fill: #7859cf;
}

.social__link:active {
  background-color: rgba(120, 89, 207, 0.25);
}
