@import '../viewport.scss';

.advantages {
  padding: 50px 20px 60px;
  text-align: center;
}

.advantages__subject {
  margin: 0;
  margin-bottom: 4px;
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  color: #7859cf;
  text-transform: uppercase;
  letter-spacing: 0.04em;
}

.advantages__title {
  margin: 0;
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
}

.advantages__list {
  padding: 0;
  margin: 0;
}

.advantages__item {
  background-repeat: no-repeat;
}

.advantages__item-title {
  margin: 0;
  padding: 99px 0 4px;
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  color: #333333;
  text-align: center;
}

.advantages__item-description {
  margin: 0;
}

.advantages__item--speed {
  background-image: url("../image/speed.svg");
  background-position: 50% 28%;
}

.advantages__item--convenience {
  background-image: url("../image/convenience.svg");
  background-position: 50% 28%;
}

.advantages__item--energy {
  background-image: url("../image/energy.svg");
  background-position: 50% 28%;
}

.advantages__item--eco {
  background-image: url("../image/eco.svg");
  background-position: 50% 28%;
}

@media (min-width: $tablet-width) {
  .advantages {
    padding: 53px 30px 106px;
  }

  .advantages__title {
    font-size: 42px;
    line-height: 64px;
    margin-bottom: 62px;
  }

  .advantages__list {
    display: grid;
    grid-template-columns: 339px 339px;
    column-gap: 30px;
    row-gap: 50px;
  }

  .advantages__item {
    padding-left: 72px;
  }

  .advantages__item-title {
    font-size: 24px;
    line-height: 36px;
    text-align: left;
    padding: 0;
    padding-bottom: 10px;
  }

  .advantages__item--speed,
  .advantages__item--convenience,
  .advantages__item--energy,
  .advantages__item--eco {
    background-position: 0 10%;
  }

  .advantages__item-description {
    font-size: 16px;
    line-height: 24px;
    text-align: left;
  }
}

