@import '../viewport.scss';

.main-navigation {
  display: flex;
  justify-content: end;
  margin: 0;
  padding: 0;
}

@media (min-width: $mobile-width-only) {
  .main-navigation {
    background-color: #ffffff;
    width: 100%;
    right: 0;
    display: none;
  }

  .main-navigation--nojs {
    display: block;
  }

  .main-navigation--closed {
    display: block;
    position: absolute;
  }

  .main-navigation--opened {
    display: block;
  }
}

@media (min-width: $tablet-width) {
  .main-navigation {
    display: block;
    position: inherit;
    background-color: transparent;
    width: 440px;
  }

  .main-navigation__toogle {
    display: none;
  }

  .main-navigation__list {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 6px;
  }
}

