.copyright {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0 30px;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
}

.copyright__title {
  margin-right: 4px;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: #bdbdbd;
}

.copyright__link {
  display: flex;
  align-items: center;
}

.copyright__icon {
  fill: #bdbdbd;
}

.copyright__link:hover .copyright__title {
  color: #7859cf;
}

.copyright__link:hover .copyright__icon {
  fill: #7859cf;
}

.copyright__link:focus .copyright__title {
  color: #7859cf;
}

.copyright__link:focus .copyright__icon {
  fill: #7859cf;
}

.copyright__link:active .copyright__title {
  color: #593cac;
}

.copyright__link:active .copyright__icon {
  fill: #593cac;
}
