@import '../viewport.scss';

.site-list {
  margin: 0;
  padding: 0;
  font-weight: 500;
  text-transform: uppercase;
  position: absolute;
  top: 60px;
  right: 0;
  width: 100%;
  background-color: #ffffff;
  z-index: 1;
}

.site-list__item {
  padding: 20px;
  border-top: 1px solid #f0ebff;
}

.site-list__item:active {
  background-color: #f0Ebff;
}

.site-list__link {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #333333;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.02em;
}

@media (min-width: $tablet-width) {
  .site-list {
    position: inherit;
  }

  .site-list__item {
    min-width: auto;
    height: 80px;
    border: none;
    padding: 0;
    display: flex;
  }

    // .site-list__item:last-child {
    //   border: none;
    //   padding-left: 34px;
    //   padding-bottom: 0;
    // }

    // .site-list__item:first-child {
    //   padding-right: 34px;
    // }

  .site-list__item--active .site-list__link {
    color: #7859cf;
    border-bottom: 2px solid #7859cf;
    padding-top: 2px;
  }

  .site-list__link {
    padding: 0 20px;
  }
}
