@import '../viewport.scss';

.filters__subject {
  margin: 0;
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  color: #7859cf;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.04em;
}

.filters__title {
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  text-align: center;
  margin-top: 4px;
  margin-bottom: 21px;
}

.filters__wrapper {
  display: flex;
  justify-content: center;
}

.filters__catalog {
  min-width: 280px;
}

.filters__price {
  padding: 0;
  margin: 0;
  border: none;
  margin-bottom: 25px;
}

.filters__price-title {
  padding: 0;
  margin-bottom: 23px;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
}

.filters__range {
  position: relative;
  margin-bottom: 36px;

  &::after {
    content: "";
    position: absolute;
    top: -1px;
    left: 0;
    width: 242px;
    height: 4px;
    background-color: #9070ec;
  }
}

.filters__range-scale {
  position: relative;
  margin: 10px 0;
  height: 2px;
  background-color: #e2e2e2;
}

.filters__range-toggle {
  position: absolute;
  width: 24px;
  height: 24px;
  background-color: #ffffff;
  outline: 2px solid #9070ec;
  outline-offset: -2px;
  border-radius: 50%;
  border: 0;
  cursor: pointer;
  z-index: 1;
}

.filters__range-toggle--min {
  top: -12px;
  left: 0;
}

.filters__range-toggle--max {
  top: -12px;
  right: 20px;
}

.filters__range-toggle--min,
.filters__range-toggle--max {

    &:hover::before {
    position: absolute;
    content: "";
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: rgba(120, 89, 207, 0.25);
    top: 4px;
    left: 4px;
  }

  &:focus::before {
    position: absolute;
    content: "";
    width: 16px;
    height: 16px;
    border-radius: 50%;
    top: 4px;
    left: 4px;
    background-color: rgba(120, 89, 207, 0.25);
  }

  &:active::before {
    position: absolute;
    content: "";
    width: 16px;
    height: 16px;
    border-radius: 50%;
    top: 4px;
    left: 4px;
    background-color: #7850cf;
  }
}

.filters__row-wrapper-inputs {
  display: flex;
  justify-content: space-between;
  margin: 0;
}

.filters__row-min,
.filters__row-max {
  padding: 5px 10px;
  width: 98px;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #333333;
  text-align: center;
  border: 2px solid #9070ec;
  border-radius: 4px;

  &:focus-within {
    outline: 2px solid #593cac;
  }
}

.filters__availability {
  margin: 0;
  padding: 0;
  border: none;
}

.filters__availability-title {
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  padding: 0;
}

.filters__availability-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.filters__availability-item {
  display: flex;
  align-items: center;
  margin-bottom: 22px;
}

.filters__label-radio {
  display: flex;
  align-items: center;
  font-size: 16px;
  line-height: 24px;
  position: relative;
  padding-left: 34px;
}

.filters__control-mark-radio {
  position: absolute;
  width: 22px;
  height: 22px;
  border: 2px solid #bdbdbd;
}

.filters__input-radio[type="radio"] + .filters__control-mark-radio {
  border-radius: 50px;
}

.filters__input-radio[type="radio"]:checked + .filters__control-mark-radio::before {
  position: absolute;
  content: "";
  width: 16px;
  height: 16px;
  top: 3px;
  left: 3px;
  background-color: #7859cf;
  border-radius: 50%;
}

.filters__input-radio[type="radio"]:checked + .filters__control-mark-radio::after {
  position: absolute;
  content: "";
  width: 22px;
  height: 22px;
  border: 2px solid #7859cf;
  border-radius: 50%;
  top: -2px;
  right: -2px;
}

.filters__input-radio[type="radio"]:disabled ~ .filters__control-mark-radio {
  border: 2px solid #e2e2e2;
}

.filters__input-radio[type="radio"]:disabled ~ .filters__label-radio {
  color: #bdbdbd;
}

.filters__group {
  margin: 0;
  padding: 0;
  border: none;
}

.filters__button {
  display: flex;
  align-items: center;
  margin: 0 auto;
  padding: 10px 15px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 24px;
  color: #ffffff;
  background-color: #7859cf;
  border: 0;
  border-radius: 4px;
  cursor: pointer;

  &:first-child {
    margin-bottom: 24px;
    margin-top: 11px;
  }

  &:last-child {
    margin-bottom: 27px;
  }

  &:hover {
    background-color: #9070ec;
  }

  &:focus {
    background-color: #9070ec;
  }

  &:active {
    background-color: #593cac;
  }

  & :disabled {
    background-color: #bdbdbd;
  }
}

.filters__button--reset {
  padding: 10px 17px;
  font-size: 14px;
  color: #7859cf;
  background-color: #ffffff;
}

.filters__button--reset:hover {
  color: #ffffff;
}

.filters__button--reset:default {
  background-color: #ffffff;
}

.filters__producer {
  margin: 0;
  padding: 0;
  border: none;
}

.filters__producer-title {
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
}

.filters__producer-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.filters__producer-item {
  display: flex;
  align-items: center;
  margin-bottom: 21px;
}

.filters__control-mark-input {
  position: absolute;
  width: 42px;
  height: 24px;
  background-color: #bdbdbd;

  &::before {
    position: absolute;
    content: "";
    width: 20px;
    height: 20px;
    background-color: #ffffff;
    border-radius: 50%;
    top: 2px;
    left: 2px;
  }
}

.filters__control-input[type="checkbox"] + .filters__control-mark-input {
  border-radius: 50px;
}

.filters__control-input[type="checkbox"]:checked + .filters__control-mark-input::before {
  position: absolute;
  content: "";
  width: 42px;
  height: 24px;
  top: 0;
  left: 0;
  background-color: #7859cf;
  border-radius: 50px;
}

.filters__control-input[type="checkbox"]:checked + .filters__control-mark-input::after {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  background-color: #ffffff;
  border-radius: 50%;
  top: 2px;
  right: 2px;
}

.filters__control-input[type="checkbox"]:disabled ~ .filters__control-mark-input {
  background-color: #e2e2e2;
}

.filters__control-input[type="checkbox"]:disabled ~ .filters__control-label {
  color: #bdbdbd;
}

.filters__control-label {
  display: flex;
  align-items: center;
  font-size: 16px;
  line-height: 24px;
  position: relative;
  padding-left: 54px;
}

@media (min-width: $tablet-width) {
  .filters__catalog {
    display: grid;
    grid-template-columns: 210px 210px 200px;
    column-gap: 25px;
  }

  .filters__group {
    display: flex;
    grid-column: 2/3;
    grid-row: 2/3;
    margin: 0 0 44px -85px;
  }

  .filters__button:first-child {
    margin: 0 30px 0 0;
  }

  .filters__button:last-child {
    margin-bottom: 0;
  }

  .filters__title {
    margin: 5px 0 20px;
    font-size: 42px;
    line-height: 64px;
  }

  .filters__wrapper {
    justify-content: space-between;
  }
}

