.contacts {
  padding-bottom: 20px;
}

.contacts__title {
  margin: 0;
  padding-bottom: 20px;
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  color: #7859cf;
  letter-spacing: 0.02em;
}

.contacts__list {
  margin: 0;
  padding: 0;
}

.contacts__address {
  display: flex;
  column-gap: 6px;

  &::before {
    content: "";
    flex-shrink: 0;
    margin-top: 3px;
    width: 13px;
    height: 16px;
    background-image: url("/image/icon-address.svg");
    background-repeat: no-repeat;
  }
}

.contacts__address-text {
  margin: 0;
  padding-bottom: 16px;
  font-style: normal;
}

.contacts__phone {
  display: flex;
  column-gap: 6px;

  &::before {
    content: "";
    flex-shrink: 0;
    margin-top: 3px;
    width: 13px;
    height: 16px;
    background-image: url("/image/icon-phone.svg");
    background-repeat: no-repeat;
  }
}

.contacts__phone-text {
  font-style: normal;
  color: #333333;
}

.contacts__maps {
  border: none;
  display: block;
  width: 100%;
  height: 362px;
  background-image: url("../image/map/map-mobile@1x.png");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
}
