@import '../viewport.scss';

.pagination {
  margin-top: 40px;
}

.pagination__list {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

.pagination__item {
  margin-left: 20px;
  width: 40px;
  height: 40px;
  background-color: #ffffff;
  border-radius: 4px;

  &:first-child {
    margin-left: 0;
  }
}

.pagination__text {
  display: none;
}

.pagination__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #7859cf;
  cursor: pointer;
}

.pagination__icon {
  fill: #7859cf;
}

.pagination__link:hover {
  color: #ffffff;
  background-color: #9070ec;
  border-radius: 4px;
}

.pagination__link:hover .pagination__icon {
  fill: #ffffff;
}

.pagination__link:active .pagination__icon {
  fill: #ffffff;
}

.pagination__link:focus .pagination__icon {
  fill: #ffffff;
}

.pagination__link:active {
  background-color: #593cac;
  border-radius: 4px;
}

.pagination__link:disabled {
  color: #ffffff;
  background-color: #e2e2e2;
  border-radius: 4px;
}

.pagination__link:focus {
  background-color: #9070ec;
  border-radius: 4px;
}

.pagination__link--current {
  font-weight: 600;
  color: #333333;
  background-color: #f8f8f8;
}

@media (min-width: $tablet-width) {
  .pagination {
    margin-top: 70px;
  }
  .pagination__list {
    justify-content: end;
  }

  .pagination__item:last-child {
    margin-left: 0;
    width: 140px;
  }

  .pagination__item:first-child {
    margin-right: 0;
    width: 140px;
  }

  .pagination__item:nth-child(2) {
    margin-left: 0;
  }

  .pagination__icon--left,
  .pagination__icon--right {
    display: none;
  }

  .pagination__text {
    display: block;
  }
}
