@import '../viewport.scss';

.product {
  display: flex;
  flex-direction: column;
  margin: 0 0 16px;
  padding: 44px 20px 20px;
  background-color: #ffffff;
  border-radius: 4px;
}

.product__link-image {
  display: block;
  margin: 0 auto;
}

.product__title {
  padding: 18px 0 5px;
  margin: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
}

.product__description {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  padding-bottom: 16px;
}

.product__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product__price {
  margin: 0;
  font-weight: 600;
  font-size: 20px;
  line-height: 32px;
}

.product__apply {
  display: flex;
  align-items: center;
  padding: 8px 20px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #ffffff;
  background-color: #7859cf;
  border-radius: 4px;
}

.product__apply:hover {
  background-color: #9070ec;
}

.product__apply:focus {
  background-color: #9070ec;
}

.product__apply:active {
  background-color: #593cac;
}

.product__apply:disabled {
  background-color: #bdbdbd;
}

.product__icon {
  margin-right: 6px;
}

@media (min-width: $tablet-width) {
  .product {
    padding: 50px 29px 30px;
    margin: 0;
  }

  .product__title {
    font-size: 24px;
    line-height: 36px;
    padding-top: 25px;
    padding-bottom: 10px;
  }

  .product__description {
    padding-bottom: 20px;
  }

}
