@import '../viewport.scss';

.sorting {
  display: flex;
  justify-content: center;
  align-items: center;
}

.sorting__top {
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  margin-right: 15px;
}

.sorting__control {
  max-width: 155px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #7859cf;
  background-color: transparent;
  border: 0;
}

@media (min-width: $tablet-width) {
  .sorting {
    justify-content: end;
    margin-bottom: 13px;
  }
}
