@import '../viewport.scss';

.control {
  display: flex;
  margin: 0;
  padding: 0;
}

.control__item {
  display: flex;
  width: 60px;
  height: 60px;
}

.control__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  fill: #333333;
}

.control__link-name {
  display: none;
}

.control__link:hover {
  background-color: #f0ebff;
}

@media (min-width: $tablet-width) {
  .control__item {
    height: 80px;
  }
}
