@import '../viewport.scss';

@media (min-width: $mobile-width-only) {
  .main-header__toogle--nojs {
    display: none;
  }

  .main-header__toogle--opened {
    display: block;
  }

  .main-header__toogle--opened::before {
    content: "";
    position: absolute;
    top: 22px;
    right: 16px;
    width: 22px;
    height: 22px;
    background-image: url("../image/burger-menu.svg");
    background-repeat: no-repeat;
  }

  .main-header__toogle--closed::before {
    content: "";
    position: absolute;
    top: 22px;
    right: 22px;
    width: 16px;
    height: 16px;
    background-image: url("../image/close.svg");
    background-repeat: no-repeat;
  }
}
.main-header__wrapper {
  display: flex;
  align-items: center;
}

.main-header__wrapper-navigation {
  display: flex;
  justify-content: center;
  width: 100%;
  align-items: center;
}

.main-header__logo {
  display: block;
  width: 32px;
  height: 36px;
}

.main-header__toogle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  cursor: pointer;
  background-color: transparent;
  border: none;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 22px;
    right: 16px;
    width: 22px;
    height: 22px;
    background-image: url("../image/burger-menu.svg");
    background-repeat: no-repeat;
  }
}
.main-header__toogle:hover {
  background-color: #f0Ebff;
}


@media (min-width: $tablet-width) {
  .main-header__toogle {
    display: none;
  }

  .main-header__logo {
    width: 202px;
  }

  .main-header__logo-mobile {
    width: 82px;
    min-height: 38px;
  }

  .main-header__wrapper {
    padding: 0 20px;
  }
}

